import React from 'react';

import type { Evidence } from '@jan6evidence/types';

import Result from './Result';

import { ResultsTable } from './evidence-results-styled-components';

type Props = {
  results: Evidence[];
  activeResult: Evidence | null;
  setActiveResult: (result: Evidence | null) => void;
};

const EvidenceTable = ({ results, activeResult, setActiveResult }: Props) => (
  <ResultsTable>
    <thead>
      <tr>
        <th className="thumbnail">Thumbnail</th>
        <th className="summary">Summary</th>
        <th className="tags">Tags</th>
        <th className="time">Time</th>
        <th className="location">Location</th>
      </tr>
    </thead>
    <tbody>
      {results.map((result) => (
        <Result
          key={result._id}
          data={result}
          isActive={activeResult === result}
          activate={() => setActiveResult(result)}
        />
      ))}
    </tbody>
  </ResultsTable>
);

export default EvidenceTable;
