import React from 'react';

import type { Suspect } from '@jan6evidence/types';

import Header, { HeaderCell } from './Header';
import Row from './Row';
import { TableWrapper, ScrollableTable } from './table-styled-components';

const HEADER_SPEC: HeaderCell[][] = [
  [
    { colSpan: 4, title: 'Suspect' },
    { colSpan: 4, title: 'Law Enforcement' },
    { rowSpan: 2, title: 'Summary' },
  ],
  [
    { title: 'Image' },
    { title: 'Hashtag(s)' },
    { title: 'Links' },
    { title: 'Tags' },
    { title: 'Wanted or Arrest #' },
    { title: "Arrested or ID'd" },
    { title: 'Profile leads' },
    { title: 'Date profile sent to FBI' },
  ],
];

const SuspectsTable = ({ suspects }: { suspects: Suspect[] }) => {
  return (
    <TableWrapper data-toggle="table">
      <ScrollableTable>
        <Header spec={HEADER_SPEC} />
        <tbody>
          {suspects.map((suspect) => (
            <Row key={suspect._id} data={suspect} />
          ))}
        </tbody>
      </ScrollableTable>
    </TableWrapper>
  );
};

export default SuspectsTable;
