import React from 'react';
import Form from 'react-bootstrap/Form';

import type { Filters, FilterChoices, Toggles } from '../../types/Filters';

import ActiveFilters from './ActiveFilters';
import FilterChoiceBox from './FilterChoiceBox';

import KeywordSearch from './KeywordSearch';
import Section from './Section';
import { Box, BoxHeader, Toggles as TogglesContainer } from './filter-box-styled-components';

type Props = {
  keywords: string[];
  filters: Filters;
  toggles: Toggles;
  choices: FilterChoices;
  onKeywordsChanged: (newKeywords: string[]) => void;
  onFilterChanged: <K extends keyof Filters>(key: K, newValue: Filters[K]) => void;
  onToggleChanged: <K extends keyof Toggles>(key: K, newValue: Toggles[K]) => void;
};

const removeOneFromSection = (section: Set<string>, id: string) => {
  section.delete(id);
  return section;
};

const FilterBox = ({
  keywords,
  filters,
  toggles,
  choices,
  onKeywordsChanged,
  onFilterChanged,
  onToggleChanged,
}: Props) => {
  const clearAll = () => {
    onFilterChanged('location', new Set());
    onFilterChanged('suspect', new Set());
    onFilterChanged('otherTags', new Set());
    onKeywordsChanged([]);
  };

  return (
    <Box>
      <BoxHeader>
        <h4>Filters</h4>
        <button type="button" onClick={clearAll}>
          Clear all
        </button>
      </BoxHeader>
      <ActiveFilters
        filters={filters}
        choices={choices}
        keywords={keywords}
        onDelete={(type: 'keywords' | keyof Filters, id) =>
          type === 'keywords'
            ? onKeywordsChanged([])
            : onFilterChanged(type, removeOneFromSection(filters[type], id))
        }
      />
      <TogglesContainer>
        <Form.Switch
          type="switch"
          id="include-untimestamped"
          label="Include evidence without a time"
          checked={toggles.includeUntimestamped}
          onChange={(evt) => onToggleChanged('includeUntimestamped', evt.target.checked)}
        />
      </TogglesContainer>
      <KeywordSearch
        onChange={(search) => onKeywordsChanged(search.split(' '))}
        value={keywords.join(' ')}
      />

      <Section label="Location">
        <FilterChoiceBox
          categoryId="location"
          choices={choices.location}
          selectedIds={filters.location}
          onChange={(params) => onFilterChanged('location', params)}
        />
      </Section>
      <Section label="Suspects">
        <FilterChoiceBox
          categoryId="suspects"
          choices={choices.suspect}
          selectedIds={filters.suspect}
          onChange={(params) => onFilterChanged('suspect', params)}
        />
      </Section>
      <Section label="Other Tags">
        <FilterChoiceBox
          categoryId="otherTags"
          choices={choices.otherTags}
          selectedIds={filters.otherTags}
          onChange={(params) => onFilterChanged('otherTags', params)}
        />
      </Section>
    </Box>
  );
};

export default FilterBox;
