import React from 'react';
import Modal from 'react-modal';

import type { Evidence } from '@jan6evidence/types';

import Tag from '../shared/Tag';
import { colors } from '../shared/constants';
import { timeFormatter } from '../shared/util';

import { ModalWrapper, Header, Content, Buttons } from './detail-styled-components';

type Props = {
  isOpen: boolean;
  evidence: Evidence;
  onClose: () => void;
};

export default function EvidenceDetail({ isOpen, evidence, onClose }: Props) {
  let ytVideo = '';
  let archiveOrgVideo = '';
  if (evidence && (evidence.videoUrl || evidence.imageUrl)) {
    const url = new URL(evidence.videoUrl || (evidence.imageUrl as string));
    /* NOTE so far all our handhacked handlers are for video */
    if (url.hostname === 'youtu.be') {
      ytVideo = url.pathname.slice(1);
    } else if (url.hostname === 'www.youtube.com') {
      ytVideo = url.searchParams.get('v') || '';
    } else if (url.hostname === 'archive.org' && evidence.videoUrl) {
      archiveOrgVideo = url.pathname.split('/')[2] || '';
    }
  }

  return (
    <Modal
      isOpen={isOpen}
      onRequestClose={onClose}
      contentLabel="Evidence Details"
      style={{ overlay: { zIndex: 1000 } }}
      className="evidence-detail-modal"
      shouldCloseOnOverlayClick
    >
      {evidence && (
        <ModalWrapper>
          <Header>
            <h2>Evidence Detail</h2>
            <Buttons>
              {/* <button title="Star" type="button" disabled>
                &#9733;
              </button> */}
              <button title="Close" type="button" className="close-button" onClick={onClose}>
                &times;
              </button>
            </Buttons>
          </Header>
          <Content>
            {evidence.imageUrl && (
              <a className="media" href={evidence.imageUrl}>
                <img
                  src={evidence.imageUrl}
                  alt={evidence.summary || evidence.locationName || 'Evidence Image'}
                />
                <span>{evidence.imageUrl}</span>
              </a>
            )}
            {evidence.videoUrl && (
              <a className="media" href={evidence.videoUrl} target="_blank" rel="noreferrer">
                {evidence.videoUrl && !ytVideo && !archiveOrgVideo && (
                  <video controls>
                    <source src={evidence.videoUrl} />
                  </video>
                )}
                {archiveOrgVideo && (
                  <iframe
                    title="embedded evidence video from archive.org"
                    src={`https://archive.org/embed/${archiveOrgVideo}`}
                    width="640"
                    height="480"
                    frameBorder="0"
                    allowFullScreen
                  />
                )}
                {ytVideo && (
                  <iframe
                    title="embedded evidence video from youtube"
                    width="560"
                    height="315"
                    src={`https://www.youtube.com/embed/${ytVideo}`}
                    frameBorder="0"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen
                  />
                )}
                <span>{evidence.videoUrl}</span>
              </a>
            )}
            <table>
              <tbody>
                <tr>
                  <th scope="row">Summary</th>
                  <td>{evidence.summary}</td>
                </tr>
                <tr>
                  <th scope="row">Tags</th>
                  <td>
                    {evidence.suspectTags &&
                      evidence.suspectTags.map((label) => (
                        <Tag
                          key={`${evidence._id}-person-${label}`}
                          id={`${evidence._id}-person-${label}`}
                          label={label}
                          color={colors.person}
                          title={label}
                        />
                      ))}
                    {evidence.otherTags &&
                      evidence.otherTags.map((label) => (
                        <Tag
                          key={`${evidence._id}-other-${label}`}
                          id={`${evidence._id}-other-${label}`}
                          label={label}
                          title={label}
                        />
                      ))}
                  </td>
                </tr>
                <tr>
                  <th scope="row">Time</th>
                  <td>
                    {`${timeFormatter(evidence.realTimeStart) || '??:??'}${
                      evidence.videoUrl
                        ? ` - ${timeFormatter(evidence.realTimeEnd) || '??:??'}`
                        : ''
                    }`}
                  </td>
                </tr>
                <tr>
                  <th scope="row">Location</th>
                  <td>{evidence.locationName}</td>
                </tr>
                <tr>
                  <th scope="row">Rationale</th>
                  <td>{evidence.rationale}</td>
                </tr>
              </tbody>
            </table>
          </Content>
        </ModalWrapper>
      )}
    </Modal>
  );
}
