import React from 'react';

import type { Filters, FilterChoices, FilterChoice } from '../../types/Filters';
import Tag from '../shared/Tag';

import { FilterPills } from './filter-box-styled-components';

type Props = {
  filters: Filters;
  choices: FilterChoices;
  keywords: string[];
  onDelete: (type: 'keywords' | keyof Filters, id: string) => void;
};

const ActiveFilters = ({ filters, choices, keywords, onDelete }: Props) => {
  const filterChoices = React.useMemo(() => {
    const result: (FilterChoice & { type: keyof Filters })[] = [];
    (Object.keys(filters) as (keyof Filters)[]).forEach((filterKey) => {
      const sectionFilters: Set<string> = filters[filterKey];
      const sectionChoices: FilterChoice[] | undefined = choices[filterKey];
      if (sectionChoices) {
        sectionFilters.forEach((id) => {
          const match = sectionChoices.find((element) => element._id === id);
          if (match) {
            result.push({ ...match, type: filterKey });
          }
        });
      }
    });
    return result;
  }, [filters, choices]);

  const search = keywords.join(' ');

  return (
    <FilterPills>
      {filterChoices.map(({ _id, type, label }) => (
        <Tag
          key={`active-${_id}`}
          id={_id}
          className={type}
          label={label}
          title={`${type[0].toUpperCase() + type.slice(1)}: ${label}`}
          onDelete={() => onDelete(type, _id)}
        />
      ))}
      {search.length > 0 && (
        <Tag
          key="active-keywords"
          id="search"
          label={search}
          className="keywords"
          title={`Keywords: ${search}`}
          onDelete={() => onDelete('keywords', search)}
        />
      )}
    </FilterPills>
  );
};

export default ActiveFilters;
