import Leaflet from 'leaflet';
import * as React from 'react';
import { Marker, Tooltip } from 'react-leaflet';

type Props = {
  position: Leaflet.LatLngExpression;
  children: React.ReactNode;
  tooltip?: string;
  tooltipOffset?: [number, number];
};

const CustomMarker = ({ children, position, tooltip, tooltipOffset = [0, 0] }: Props) => {
  const ref = React.useRef<HTMLDivElement | null>(null);
  const [element, setElement] = React.useState<HTMLDivElement | null>(null);

  React.useEffect(() => {
    if (ref.current && ref.current !== element) {
      setElement(ref.current);
    }
  });

  return (
    <>
      <template>
        <div ref={ref}>{children}</div>
      </template>
      {element ? (
        <Marker position={position} icon={Leaflet.divIcon({ html: element, className: '' })}>
          {tooltip != null && tooltip !== '' ? (
            <Tooltip offset={tooltipOffset}>{tooltip}</Tooltip>
          ) : null}
        </Marker>
      ) : null}
    </>
  );
};

export default CustomMarker;
