import React from 'react';

import { CrimeCategories, CrimeCategory } from './row-styled-components';

export type CategoryType = {
  id: string;
  name: string;
  color?: string;
  present?: boolean;
};
export type CategoriesType = CategoryType[];

interface Props {
  id: string;
  data: CategoriesType;
  // addCategory: (id: string) => null,
  // removeCategory: (id: string) => null,
}

const Categories: React.FunctionComponent<Props> = ({
  id,
  data /* addCategory, removeCategory, */,
}: Props) => {
  return (
    <>
      <CrimeCategories>
        {data.map((category) => {
          if (category.present) {
            return (
              <CrimeCategory
                key={`${id}-${category.id}`}
                style={{ backgroundColor: category.color }}
              >
                {category.name}
              </CrimeCategory>
            );
          }
          return null;
        })}
      </CrimeCategories>
    </>
  );
};

export default Categories;
