import Leaflet from 'leaflet';
import type { LatLngBounds } from 'leaflet';
import React from 'react';
import Modal from 'react-modal';
import { useParams, useHistory } from 'react-router-dom';

import type { Evidence } from '@jan6evidence/types';

import { evidence } from '../data';

import { Filters, Toggles } from '../types/Filters';

import EvidenceDetail from './EvidenceDetail';
import { EvidenceMap, EvidenceTable, EvidenceTimeline } from './EvidenceMain';
import FilterBox from './FilterBox';
import { applyFilters, buildFilterChoices } from './FilterBox/filterUtils';
import {
  EvidenceResultsWrapper,
  EvidenceTableWrapper,
  ButtonLink,
  EvidenceCount,
} from './table-styled-components';

const FILTER_CHOICES = buildFilterChoices(evidence);

Modal.setAppElement('#root');

const EvidenceApp = () => {
  const [filters, setFilters] = React.useState<Filters>({
    location: new Set<string>(),
    suspect: new Set<string>(),
    otherTags: new Set<string>(),
  });
  const [keywords, setKeywords] = React.useState<string[]>([]);
  const [toggles, setToggles] = React.useState<Toggles>({
    includeUntimestamped: true,
  });
  const [startEndTimes, setStartEndTimes] = React.useState<[Date, Date] | null>(null);
  const { detailId } = useParams<{ detailId?: string }>();
  const history = useHistory();
  const closeModal = React.useCallback(() => history.push(`/evidence`), [history]);

  const [mapBounds, setMapBounds] = React.useState<LatLngBounds | null>(null);

  const results = applyFilters(evidence, filters, keywords, toggles, startEndTimes);
  const [activeResult, setActiveResult] = React.useState<Evidence | null>(null);

  const mapResults = results.filter((result) => {
    if (!mapBounds) return true;

    const { lat, lng } = result.location || {};
    if (!lat || !lng) return false;

    const latLng = Leaflet.latLng([lat, lng]);
    return mapBounds.contains(latLng);
  });

  React.useEffect(() => {
    const onKeyUp = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        setActiveResult(null);
      }
    };
    window.addEventListener('keyup', onKeyUp);
    return () => window.removeEventListener('keyup', onKeyUp);
  });

  return (
    <div>
      <FilterBox
        onFilterChanged={(section, newContent) => {
          setActiveResult(null);
          setMapBounds(null);
          setFilters((oldFilters) => ({ ...oldFilters, [section]: newContent }));
        }}
        onKeywordsChanged={(newKeywords) => setKeywords(newKeywords)}
        filters={filters}
        choices={FILTER_CHOICES}
        keywords={keywords}
        toggles={toggles}
        onToggleChanged={(toggle, newValue) => {
          setToggles((oldToggles) => ({ ...oldToggles, [toggle]: newValue }));
        }}
      />
      <EvidenceResultsWrapper>
        <h1>Evidence Database</h1>
        <EvidenceMap
          evidence={results}
          activeResult={activeResult}
          setActiveResult={setActiveResult}
          onChange={(bounds) => setMapBounds(bounds)}
        />
        <EvidenceTimeline
          startEndTimes={startEndTimes}
          onChangeTimes={setStartEndTimes}
          // We re-filter here, ignoring time time filter, so that we still show
          // evidence on the timeline that isn't in the selected time range
          evidence={applyFilters(evidence, filters, keywords, toggles, null)}
        />
        <EvidenceCount>
          {results.length > mapResults.length ? (
            <>
              {mapResults.length} bits of mapped evidence (
              <ButtonLink
                type="button"
                onClick={(event) => {
                  event.preventDefault();
                  setMapBounds(null);
                }}
              >
                show all {results.length} bits of evidence instead
              </ButtonLink>
              )
            </>
          ) : (
            <>{mapResults.length} bits of evidence</>
          )}
        </EvidenceCount>
        <EvidenceTableWrapper>
          <EvidenceTable
            results={mapResults}
            activeResult={activeResult}
            setActiveResult={setActiveResult}
          />
        </EvidenceTableWrapper>
      </EvidenceResultsWrapper>
      <EvidenceDetail
        isOpen={!!detailId}
        evidence={results.find((r) => r._id === detailId)!}
        onClose={closeModal}
      />
    </div>
  );
};

export default EvidenceApp;
