import React, { useState } from 'react';
import { Slider, Rail, Handles, Tracks, Ticks } from 'react-compound-slider';

import type { Evidence } from '@jan6evidence/types';

import { numberTimeFormatter } from '../shared/util';

import {
  EvidencePoint,
  Range,
  SliderRail,
  Handle,
  Track,
  Tick,
  EvidenceStripe,
} from './TimelineHelpers';
import { TimelineWrapper } from './evidence-results-styled-components';

const sliderStyle = {
  position: 'relative',
  width: '100%',
};

// DOMAIN_START and DOMAIN_END are the start and end times of the timeline,
// in milliseconds since the unix epoch.
const DOMAIN_START = Date.UTC(2021, 0, 6, 15, 0, 0); // 10AM Eastern
const DOMAIN_END = Date.UTC(2021, 0, 7, 1, 0, 0); // 8PM Eastern

// Returns min if (val < min), max if (val > max), and val otherwise.
function clamp(val: number, min: number, max: number): number {
  return Math.min(Math.max(val, min), max);
}

function buildEvidencePoints(results: Evidence[]): EvidencePoint[] {
  return results
    .filter((result) => !!result.realTimeStart)
    .map((result) => {
      const value = clamp(result.realTimeStart!.getTime(), DOMAIN_START, DOMAIN_END);

      let endValue: number | undefined;
      if (result.realTimeEnd) {
        // we have a specific timestamp for the end of the evidence
        endValue = clamp(result.realTimeEnd.getTime(), DOMAIN_START, DOMAIN_END);
      } else if (result.videoEndTimepoint) {
        // we don't have a timestamp, but we do have how long the video evidence
        // is -- so we just add that to the start time
        const evidenceLengthMillis = result.videoEndTimepoint * 1000;
        endValue = value + evidenceLengthMillis;
      }

      return {
        id: result._id,
        value,
        endValue,
        percent: ((value - DOMAIN_START) / (DOMAIN_END - DOMAIN_START)) * 100,
        endPercent: endValue
          ? ((endValue - DOMAIN_START) / (DOMAIN_END - DOMAIN_START)) * 100
          : undefined,
        type: result.imageUrl ? 'photo' : 'video',
        summary: result.summary,
      };
    });
}

type EvidenceTimelineProps = {
  startEndTimes: [Date, Date] | null;
  onChangeTimes: (newTimes: [Date, Date] | null) => void;
  evidence: Evidence[];
};

export default function EvidenceTimeline({
  startEndTimes,
  onChangeTimes,
  evidence,
}: EvidenceTimelineProps) {
  // The parent component passes in the currently-selected time range. We also
  // maintain this local state, which has the start/end values as you're dragging
  // the sliders. Once the user releases the slider, we pass the new values up to
  // the parent and discard this state.
  const [draggingValues, setDraggingValues] = useState<[number, number] | null>(null);

  const domain: Range = [DOMAIN_START, DOMAIN_END];
  let values: Range;
  if (draggingValues) {
    values = draggingValues;
  } else if (startEndTimes) {
    values = [startEndTimes[0].getTime(), startEndTimes[1].getTime()];
  } else {
    values = [DOMAIN_START, DOMAIN_END];
  }

  function onChange(newValues: readonly number[]) {
    if (newValues[0] === DOMAIN_START && newValues[1] === DOMAIN_END) {
      onChangeTimes(null);
    } else {
      onChangeTimes([new Date(newValues[0]), new Date(newValues[1])]);
    }
  }

  const tickValues = [];
  let range = DOMAIN_END - DOMAIN_START;
  while (range >= 0) {
    tickValues.push(range + DOMAIN_START);
    range -= 30 * 60 * 1000; // 30 minutes between ticks
  }

  const evidencePoints = buildEvidencePoints(evidence);

  return (
    <TimelineWrapper>
      <div className="selected-time">
        {`${numberTimeFormatter(values[0])} - ${numberTimeFormatter(values[1])}`}
      </div>
      <Slider
        mode={1}
        step={5}
        domain={domain}
        reversed={false}
        rootStyle={sliderStyle}
        onUpdate={(newValues) => setDraggingValues([newValues[0], newValues[1]])}
        onChange={onChange}
        values={values}
      >
        <Rail>{({ getRailProps }) => <SliderRail getRailProps={getRailProps} />}</Rail>
        <Handles>
          {({ handles, getHandleProps }) => (
            <div className="slider-handles">
              {handles.map((handle) => (
                <Handle
                  key={handle.id}
                  handle={handle}
                  domain={domain}
                  getHandleProps={getHandleProps}
                  isRight={handle.value > values[0]}
                />
              ))}
            </div>
          )}
        </Handles>
        {evidencePoints.map((item) => (
          <EvidenceStripe key={`evidence-stripe-${item.id}`} stripe={item} />
        ))}
        <Tracks left={false} right={false}>
          {({ tracks, getTrackProps }) => (
            <div className="slider-tracks">
              {tracks.map(({ id, source, target }) => (
                <Track key={id} source={source} target={target} getTrackProps={getTrackProps} />
              ))}
            </div>
          )}
        </Tracks>
        <Ticks values={tickValues}>
          {({ ticks }) => (
            <div className="slider-ticks">
              {ticks.map((tick) => (
                <Tick key={tick.id} tick={tick} count={ticks.length} />
              ))}
            </div>
          )}
        </Ticks>
      </Slider>
      <div>{`${numberTimeFormatter(values[0])} - ${numberTimeFormatter(values[1])}`}</div>
    </TimelineWrapper>
  );
}
