import classnames from 'classnames';
import React from 'react';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import Form from 'react-bootstrap/Form';
import ToggleButton from 'react-bootstrap/ToggleButton';

import type { FilterChoice } from '../../types/Filters';

type Props = {
  categoryId: string;
  choices: FilterChoice[];
  selectedIds: Set<string>;
  onChange: (choiceIds: Set<string>) => void;
};

const FilterChoiceBox = ({ categoryId, choices, selectedIds, onChange }: Props) => {
  const [filter, setFilter] = React.useState<string>('');

  const toggleSelection = (id: string) => {
    const newSelected = new Set(selectedIds);
    if (selectedIds.has(id)) {
      newSelected.delete(id);
    } else {
      newSelected.add(id);
    }
    onChange(newSelected);
  };

  return (
    <>
      {choices.length > 8 && (
        <Form.Control
          type="text"
          placeholder="Filter..."
          onChange={(e) => setFilter(e.target.value)}
          value={filter}
          size="sm"
          className="choice-filter"
        />
      )}
      <ButtonGroup toggle vertical>
        {choices.map((choice) => (
          <ToggleButton
            key={`${categoryId}-choice-${choice._id}`}
            type="checkbox"
            variant="secondary"
            checked={selectedIds.has(choice._id)}
            value="1"
            onChange={() => toggleSelection(choice._id)}
            className={classnames({
              hidden:
                /* selectedIds.has(choice.id) || // TODO should chosen items disappear or go? */
                !choice.label.toLowerCase().includes(filter.toLowerCase()),
            })}
          >
            {choice.label}
          </ToggleButton>
        ))}
      </ButtonGroup>
    </>
  );
};

export default FilterChoiceBox;
