import React from 'react';
import { Route, Switch } from 'react-router';

import EvidenceApp from './EvidenceApp';
import InstructionsPage from './InstructionsPage';
import SuspectsApp from './SuspectsApp';

function Routes() {
  return (
    <Switch>
      <Route path="/" exact component={InstructionsPage} />
      <Route path="/suspects" component={SuspectsApp} />
      <Route path="/evidence" exact component={EvidenceApp} />
      <Route path="/evidence/detail/:detailId" component={EvidenceApp} />
    </Switch>
  );
}

export default Routes;
