import React from 'react';
import styled from 'styled-components';

import type { Suspect } from '@jan6evidence/types';

import Categories from './Categories';

import { LinksCellWrapper, SummaryCellWrapper, HashtagCellWrapper } from './row-styled-components';

const SuspectPhoto = styled.img`
  max-width: 100px;
  max-height: 100px;
`;

type Props = {
  data: Suspect;
};

const ImageCell = ({ data }: Props) => {
  if (!data.primaryPhotoUrl) {
    return null;
  }

  const regexMatch = data.primaryPhotoUrl?.match(/https:\/\/drive.google.com\/file\/d\/(\w+)\//);

  let photoURL = data.primaryPhotoUrl;
  if (regexMatch) {
    photoURL = `https://drive.google.com/uc?export=view&id=${regexMatch[1]}`;
  }

  return (
    <td>
      <a href={photoURL} target="_blank" rel="noopener noreferrer">
        <SuspectPhoto src={photoURL} alt="" />
      </a>
    </td>
  );
};

const HashtagLink = ({ hashtag }: { hashtag: string }) => {
  let formattedHashtag = hashtag;
  if (!formattedHashtag.startsWith('#')) {
    formattedHashtag = `#${hashtag}`;
  }

  return (
    <a
      href={`https://twitter.com/search?q=${encodeURIComponent(formattedHashtag)}`}
      target="_blank"
      rel="noopener noreferrer"
    >
      {hashtag}
    </a>
  );
};

const HashtagCell = ({ data }: Props) => (
  <HashtagCellWrapper>
    <HashtagLink hashtag={data.primaryHashtag} />
    {data.alternateHashtags.length > 0 && (
      <p className="alternates">
        {data.alternateHashtags.map((hashtag) => (
          <span className="alternate">
            <HashtagLink hashtag={hashtag} />
          </span>
        ))}
      </p>
    )}
  </HashtagCellWrapper>
);

const LinksCell = ({ data }: Props) => {
  const links = [
    {
      label: 'Composites',
      url: data.compositeURL,
    },
    {
      label: 'Files',
      url: data.filesUrl,
    },
    {
      label: 'Group Files',
      url: data.filesForGroupUrl,
    },
  ].filter(({ url }) => url);

  return (
    <LinksCellWrapper>
      {links.map(({ label, url }) => (
        <p key={label}>
          <a href={url} target="_blank" rel="noopener noreferrer">
            {label}
          </a>
        </p>
      ))}
    </LinksCellWrapper>
  );
};

const TagsCell = ({ data }: Props) => {
  const tags = [
    {
      present: !!data.category,
      id: 'category',
      name: data.category!,
      color: '#ccc',
    },
    {
      present: data.isViolent,
      id: 'violent',
      name: 'Violence',
      color: '#dd4b4b',
    },
    {
      present: data.isVandal,
      id: 'vandalism',
      name: 'Vandalism',
      color: '#ffba5f',
    },
    {
      present: data.isOrganizer,
      id: 'organizer',
      name: 'Organizer',
      color: '#ced62a',
    },
    {
      present: data.hasEarpieceOrRadio,
      id: 'earpiece',
      name: 'Earpiece / Radio',
      color: '#bec4ff',
    },
    {
      present: data.hasHelmetOrArmor,
      id: 'helmet',
      name: 'Helmet / Armor',
      color: '#9084ff',
    },
  ];

  return (
    <td>
      <Categories id={data._id} data={tags} />
    </td>
  );
};

const TwitterUserLink = ({ username }: { username: string }) => {
  let formattedUsername = username;
  if (formattedUsername.startsWith('@')) {
    formattedUsername = username.slice(1);
  }

  return (
    <a
      href={`https://twitter.com/${encodeURIComponent(formattedUsername)}`}
      target="_blank"
      rel="noopener noreferrer"
    >
      {username}
    </a>
  );
};

const ProfileLeadsCell = ({ data }: Props) => {
  return (
    <td>
      {data.profileLeads.map((lead) => (
        <p key={lead}>
          <TwitterUserLink username={lead} />
        </p>
      ))}
    </td>
  );
};

const SummaryCell = ({ data }: Props) => {
  const sections = [
    { header: 'Brief summary of observed actions', body: data.actionsSummary },
    { header: 'Appearance notes', body: data.appearanceSummary },
    { header: 'Associated with', body: data.associations.join(', ') },
    {
      header: 'URLs for additional information / images',
      body: data.otherUrls,
    },
  ].filter(({ body }) => body);

  return (
    <SummaryCellWrapper>
      {sections.map(({ header, body }) => (
        <p key={header}>
          <strong>{header}</strong>
          <br />
          {body}
        </p>
      ))}
    </SummaryCellWrapper>
  );
};

// TODO differentiate by cell type based on columnIndex
const Row = ({ data }: Props) => (
  <tr data-title={`suspect-${data._id}`}>
    <ImageCell data={data} />
    <HashtagCell data={data} />
    <LinksCell data={data} />
    <TagsCell data={data} />
    <td>{data.lawEnforcementIdentifiers}</td>
    <td>{data.arrestedOrPubliclyId}</td>
    <ProfileLeadsCell data={data} />
    <td>{data.reportedToFbiDate}</td>
    <SummaryCell data={data} />
  </tr>
);

export default Row;
