import React from 'react';

import { TableHead, StickyTH } from './table-styled-components';

export type HeaderCell = {
  rowSpan?: number;
  colSpan?: number;
  title: string;
  ref?: (instance: HTMLTableHeaderCellElement | null) => void;
  width?: number;
};

type Props = {
  spec: HeaderCell[][];
  adjustDimensions?: boolean;
};

const Header = ({ spec }: Props) => (
  <TableHead>
    {spec.map((tr, rowNum) => (
      // eslint-disable-next-line react/no-array-index-key
      <tr key={rowNum}>
        {tr.map((cellData) => {
          const colSpan = cellData.colSpan || 1;
          return (
            <StickyTH
              rowSpan={cellData.rowSpan || 1}
              colSpan={colSpan}
              style={{ top: `${rowNum * 25}px` }}
              key={cellData.title}
            >
              <div className={`${colSpan > 1 ? 'parent-category' : ''}`}>{cellData.title}</div>
            </StickyTH>
          );
        })}
      </tr>
    ))}
  </TableHead>
);

export default Header;
