import type { Evidence } from '@jan6evidence/types';

import type { FilterChoices, Filters, Toggles } from '../../types/Filters';

export function buildFilterChoices(evidence: Evidence[]): FilterChoices {
  const allLocations = new Set<string>();
  const allSuspects = new Set<string>();
  const allOtherTags = new Set<string>();
  evidence.forEach((item) => {
    if (item.locationName) {
      allLocations.add(item.locationName);
    }

    if (item.suspectTags) {
      item.suspectTags.forEach((tag) => allSuspects.add(tag));
    }

    if (item.otherTags) {
      item.otherTags.forEach((tag) => allOtherTags.add(tag));
    }
  });

  return {
    location: Array.from(allLocations)
      .sort()
      .map((s) => ({ _id: s, label: s })),
    suspect: Array.from(allSuspects)
      .sort()
      .map((s) => ({ _id: s, label: s })),
    otherTags: Array.from(allOtherTags)
      .sort()
      .map((s) => ({ _id: s, label: s })),
  };
}

export function applyFilters(
  evidence: Evidence[],
  filters: Filters,
  rawKeywords: string[],
  toggles: Toggles,
  startEndTimes: [Date, Date] | null
): Evidence[] {
  const keywords = rawKeywords.filter((s) => s);

  return evidence.filter((item) => {
    if (filters.location.size > 0) {
      if (!item.locationName) {
        return false;
      }

      if (!filters.location.has(item.locationName)) {
        return false;
      }
    }

    if (filters.suspect.size > 0) {
      if (!item.suspectTags) {
        return false;
      }

      if (!item.suspectTags.some((tag) => filters.suspect.has(tag))) {
        return false;
      }
    }

    if (filters.otherTags.size > 0) {
      if (!item.otherTags) {
        return false;
      }

      if (!item.otherTags.some((tag) => filters.otherTags.has(tag))) {
        return false;
      }
    }

    if (keywords.length > 0) {
      if (
        ![
          item.locationName,
          ...(item.otherTags || []),
          ...(item.suspectTags || []),
          item.rationale,
          item.summary,
        ].some((itemField) => {
          if (!itemField) {
            return false;
          }

          return keywords.some(
            (keyword) => itemField.toLocaleLowerCase().indexOf(keyword.toLocaleLowerCase()) >= 0
          );
        })
      ) {
        return false;
      }
    }

    if (!toggles.includeUntimestamped && !item.realTimeStart) {
      return false;
    }

    if (startEndTimes && item.realTimeStart) {
      if (item.realTimeStart < startEndTimes[0] || item.realTimeStart > startEndTimes[1]) {
        return false;
      }
    }

    return true;
  });
}
